
.subheader-content {
    display: flex;
    justify-content: space-between;

    &__subtitle {
        margin-top: 43px;
    }

    &__btns {
        margin-top: 40px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex: 1 1 auto;
    }

    &__btn {
        margin-left: 10px;

        svg {
            width: 34px;
            height: 34px;
        }
    }

    .special-table__search-block {
        flex: 1 1 auto;
        display: flex;
        justify-content: flex-end;
    }

    .special-table__search {
        height: 34px;
        border-radius: 8px;
        border: 1.6px solid #64B6AC;
        width: unset;
        flex: 1 1 auto;
        max-width: 400px;

        .special-table__search-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            left: 5px;

            svg {
                margin-bottom: 3px;
            }
        }
    }

    .special-table__search-btn {
        width: 34px;
        height: 34px;

        svg {
            width: 20px;
            height: 20px;     
            stroke: #64B6AC;
            margin-top: 5px;     
        }        
    }

    .special-table__search-show {
        border-radius: 8px;
        border: 1.6px solid #64B6AC;
        margin-right: 0;

        svg {
            margin-top: 3px;            
        }
    }

    .special-table__search-close {
        right: 7px;

        svg {
            stroke: #64B6AC;
        }
    }
}

.compared-table {

    &__wrapp {
        overflow-y: hidden;       
    }

    &__product {
        min-width: 1555px;
        width: 100%;
    }

    &__list {
    }

    &__item {
        box-shadow: none;
        border-radius: none;
        background: none;
        padding: 0;           

        &:nth-child(1),
        &:nth-child(2) {
            .image-viewer {
                top: 0;
                transform: translate(0, 0);
              }
        }

        &:nth-last-child(1),
        &:nth-last-child(2) {
            .image-viewer {
                top: unset;
                bottom: 0;
                transform: translate(0, 0);
              }
        }
    }

    &__left {
        display: flex;
        flex: 1 1 709px;
        justify-content: space-between;
    }

    &__right {
        display: flex;
        flex: 1 1 709px;
        justify-content: space-between;
    }

    &__action {
        flex: 0 0 74px;
    }

    &__block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 20px;
        color: var(--gray);
        height: 60px;
        background: #FFFFFF;
        box-shadow: 0px 4px 10px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;

        &_red {
            background: #FBCACA;
        }


        &:not(:last-child) {
            margin-right: 10px;
        }
    }

    .table {

        &__product-item {
            &:first-of-type {
                padding: 7px 10px 6px 20px;
                padding: 0;
                justify-content: space-between;

                .compared-table__left {
                    padding-right: 20px;
                    padding: 10px 20px;
                    //margin-right: 10px;
                }

                .compared-table__right {
                    padding-right: 20px;
                    padding: 10px 20px;
                    margin: 0 10px;
                }

                .compared-table__action {
                    padding-right: 20px;
                    padding: 10px 10px;
                }

                .table__product-actions {
                    justify-content: flex-end;
                    margin-right: 0;
                }
            }            
        }

        &__product-amount {
        }
    
        &__btn {
            justify-content: center;
        }
    
        &__product-art {
            width: 160px;
            margin-right: 15px;
            font-weight: 500;
            font-size: 14px;
            line-height: 18px;
            color: #143127;
        }

        &__product-partner-art {
            width: 200px;
            margin-right: 15px;
            font-weight: 500;
            font-size: 14px;
            line-height: 18px;
            color: #143127;
        }
    
        &__product-photo {
            width: 40px;
            height: 40px;
            margin-right: 15px;
            position: relative;            

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }

            .image-viewer {
                img {
                    border-radius: 0;
                }
            }
        }
    
        &__product-title {
            width: 400px;
            font-size: 14px;
            line-height: 17px;
            color: #626F6A;
        }
    
        &__product-actions {
            width: 54px;
        }
    }
}

.compared-controls {
    padding: 20px 0 43px;

    .controls__wrapper {
        @media (max-width: 993px) {
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-start;
            gap: 20px;
        }

        .pagination {
            @media (max-width: 768px) {
                order: 1;
                justify-content: flex-start;
            }
        }

        .show {
            @media (max-width: 993px) {
                margin-left: 15px;
            }            

            @media (max-width: 768px) {
                order: 2;
            }
        }
    }    
}