.comparison-table {

    &__wrapp {
        padding-bottom: 20px;
        margin-bottom: 10px;
    }

    &__sublist {
        max-height: 350px;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 4px;
            height: 8px;
            background: rgba(98, 111, 106, 0.5);
        }
    
        &::-webkit-scrollbar-thumb {
            border-radius: 100px;
            background: #FDC300;
        }

        //.comparison-table__item {

        //    &.table__product-item {
        //        &:first-of-type {
        //            height: 60px;
        //            background: #FFFFFF;
        //            box-shadow: 0px 4px 10px -5px rgba(213, 213, 213, 0.64);
        //            border-radius: 20px 20px 20px 20px;
        //            margin-bottom: 10px;

        //            .table__product-photo {
        //                height: 40px;
        //                width: 40px;
        //            }
        //        }
        //    }
        //}        
    }

    &__subitem {
        width: 100%;
        height: 60px;
        background: #FFFFFF;
        box-shadow: 0px 4px 10px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        margin-bottom: 10px;

        &.active {
            background: rgba(253, 195, 0, 0.15);
        }

        &:hover {
            background: rgba(0, 0, 0, 0.05);
        }

        &:nth-child(1) {
            .image-viewer {
                top: 0;
                transform: translate(0, 0);
              }
        }

        &:nth-last-child(1) {
            .image-viewer {
                top: unset;
                bottom: 0;
                transform: translate(0, 0);
            }

            .table__product-titlefull {
                margin-top: 0;
                bottom: 100%;
            }
        } 
    }

    &__partner-art {
        width: 160px;
        justify-content: center;
        display: flex;
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: #143127;
    }

    &__photo {
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        position: relative;            

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }

        .image-viewer {
            img {
                border-radius: 0;
            }
        }
    }

    &__title {
        width: 380px;
        display: flex;
        justify-content: center;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #626F6A;
        position: relative;

        span {
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .table__product-titlefull {
            margin-top: 20px;
        }

        &:hover {
            cursor: pointer;
      
            .table__product-titlefull {
              opacity: 1;
              visibility: visible;
            }
        }
    }

    &__cat {
        width: 206px;
        display: flex;
        justify-content: center;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #626F6A;
    }

    &__price {
        width: 100px;
        display: flex;
        justify-content: center;
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: #143127;
    }

    &__availability {
        width: 50px;
        display: flex;
        justify-content: center;

        &_yes {
            display: block;
            width: 20px;
            height: 20px;
            background: #CCEFC4;
            box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
            border-radius: 50%;
        }

        &_no {
            display: block;
            width: 20px;
            height: 20px;
            background: #FBCACA;
            box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
            border-radius: 50%;
        }
    }

    &__result {
        display: flex;
        justify-content: flex-end;
        gap: 15px;
    }

    &__total,
    &__found,
    &__show-all {
        margin-top: 5px;
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: #143127;        

        span {
            font-weight: 400;
            color: #626F6A;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        flex: 1 1 auto;       
    }

    &__show-all {
        display: flex;
        align-items: center;

        svg {
            margin-right: 5px;
            width: 12px;
            height: 12px;
        }
    }

    .table {

        &__wrapp {
            overflow-y: hidden;  
        }
    
        &__product {
            min-width: 1136px;
            width: 100%;
        }
    
        &__product-item {
            &:first-of-type {
                padding: 7px 20px 6px 20px;
                justify-content: space-between;
                border-radius: 20px 0 0 20px;

                .table__product-photo {
                    height: unset;
                }

                .comparison-table__left {
                    padding: 0 20px;
                }
            }
        }
    
        &__product-partner-art {
            width: 160px;
            justify-content: center;
            display: flex;
        }
    
        &__product-photo {
            width: 40px;
            height: 40px;
            display: flex;
            justify-content: center;
            position: relative;            

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }

            .image-viewer {
                img {
                    border-radius: 0;
                }
            }
        }
    
        &__product-title {
            width: 380px;
            display: flex;
            justify-content: center;
        }
    
        &__product-cat {
            width: 206px;
            display: flex;
            justify-content: center;
        }
    
        &__product-price {
            width: 100px;
            display: flex;
            justify-content: center;
        }
    
        &__product-availability {
            width: 50px;
            display: flex;
            justify-content: center;
    
            &_yes {
                display: block;
                width: 20px;
                height: 20px;
                background: #CCEFC4;
                box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
                border-radius: 50%;
            }

            &_no {
                display: block;
                width: 20px;
                height: 20px;
                background: #FBCACA;
                box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
                border-radius: 50%;
            }
        }
    }
}

.card-comparison {
    flex: 0 0 394px;

    @media (max-width: 768px) {
        margin-top: 10px;
    }

    &__header {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--light-green);
        height: 30px;
        border-radius: 0 20px 20px 0;
        position: relative;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: -12px;
            width: 14px;
            height: 30px;
            background-color: var(--light-green);
        }
    }

    &__title {
    }

    &__wrapper {
        padding: 0 20px 30px;
        max-height: 100%;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 4px;
            height: 8px;
            background: #fff;
        }
    
        &::-webkit-scrollbar-thumb {
            border-radius: 100px;
            background: #FDC300;
        }
    }

    &__body {
        background: #fff;
        margin-top: -15px;
        padding: 35px 0 5px;
        border-radius: 0 0 20px 20px;
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        height: 371px;
        overflow: hidden;       
    }
}


.compare {
    display: flex;
    align-items: center;
    overflow-y: auto;
    margin-bottom: -25px;
    padding-bottom: 15px;

    &__btns {
    }

    &__btn {
        white-space: nowrap;
        margin-right: 20px;
    }

    &__table {
        margin-right: 10px;
        flex: 1 1 auto;
        min-width: 925px;
    }

    &__items {
        display: flex;
        flex-direction: column;
        background: #FFFFFF;
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;
        overflow: hidden;
        width: 100%;        
    }

    &__item {
        display: flex;
        align-items: center;
        height: 40px;
        padding: 0 30px;

        &:first-child {
            border-bottom: 1px solid #ACDED8;
        }
    }

    &__art {
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: #143127;
        flex: 0 0 150px;
        margin-right: 10px;
    }

    &__title {
        font-size: 14px;
        line-height: 17px;
        color: #626F6A;
        flex: 1 1 auto;
    }

    &__checkboxes {
        flex: 0 0 394px;
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    &__check {
        flex: 1 1 47%;
    }

    &__modal {
        max-width: 740px;
    }
}
